import React, { useCallback, useState } from "react";
import {
  InputContainer,
  Icon,
  Label,
  HintContainer,
  ErrorLabel,
  HintLabel,
} from "../TextInput/Atom.tsx";
import { Input } from "./Atom.tsx";

import RNSwitch from "react-switch";

export interface SwitchProps {
  onChange?: (value: any) => void;
  value?: any;
  style?: any;
  name?: string;
  customRef?: any;
  isFormInputElement?: boolean;
  required?: boolean;
  hint?: string;
  imageSrc?: any;
  label?: string;
  conditionValue?: any;
  conditionCriteria?: any;
}

export interface RefProps {
  getValue: () => string;
  validate: () => any;
  name: string;
}

const Switch = (props: SwitchProps) => {
  const {
    // onChange,
    value,
    customRef,
    name,
    required,
    hint,
    imageSrc,
    label,
  } = props;
  const getInitValue = useCallback(() => {
    return value || false;
  }, [value]);

  const [inputValue, setInputValue] = useState<any>(getInitValue());
  const [error, setError] = useState({ error: false, message: "" });

  const validate = useCallback(
    (input: any) => {
      let newState = {
        error: false,
        message: "",
      };

      if (typeof input === "undefined" && required) {
        newState = {
          error: true,
          message: label + " is missing",
        };
      }
      setError(newState);
      return newState;
    },
    [label, required]
  );

  const onChangeValue = (e: any) => {
    if(props?.conditionValue){
      if(props?.conditionValue == props?.conditionCriteria){
        return 
      }
    }
    validate(e);
    setInputValue(e);
  };

  React.useEffect(() => {
    customRef({ value: inputValue, validate, name: name || "" });
  }, [customRef, inputValue, name, validate]);

  React.useEffect(() => {
    setInputValue(value || false);
  }, [value]);

  React.useEffect(()=>{
    if(props?.conditionValue){
      if(props?.conditionValue == props?.conditionCriteria){
        setInputValue(true)
      }
    }
  },[props?.conditionValue])

  return (
    <>
      <InputContainer>
        {imageSrc ? <Icon src={imageSrc} alt="image" /> : null}
        {label ? (
          <Label>
            {required && <span className="required">* </span>} {label}:{" "}
          </Label>
        ) : null}
        <RNSwitch
          handleDiameter={20}
          onColor="#c7efda"
          offColor="#FF767C"
          onHandleColor="#6bd8a4"
          uncheckedIcon={false}
          checkedIcon={false}
          boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
          activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
          height={14}
          width={28}
          onChange={onChangeValue}
          checked={inputValue}
        />
        {inputValue ? <Input>{`${inputValue && "Active"}`} </Input> : null}
      </InputContainer>
      <HintContainer>
        {error.error ? <ErrorLabel>{error.message}</ErrorLabel> : <div />}
        {hint ? <HintLabel>{hint}</HintLabel> : <div />}
      </HintContainer>
    </>
  );
};

export default Switch;
